// Utilitaire pour grer les mises  jour de l'application
class UpdateNotifier {
  constructor() {
    this.updateAvailable = false;
    this.updateCallback = null;
    this.checkInterval = null;
  }

  // Dmarrer la vrification des mises  jour
  startChecking(callback) {
    this.updateCallback = callback;
    
    // Vrifier toutes les 5 minutes
    this.checkInterval = setInterval(() => {
      this.checkForUpdates();
    }, 5 * 60 * 1000);
    
    // Vrification initiale
    this.checkForUpdates();
  }

  // Vrifier s'il y a une mise  jour
  checkForUpdates() {
    // Vrifier si le service worker a une mise  jour
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.ready.then((registration) => {
        registration.update();
      });
    }

    // Vrifier si l'index.html a chang (technique simple)
    fetch('/index.html', { cache: 'no-cache' })
      .then(response => response.text())
      .then(html => {
        const currentHash = this.getHash(html);
        const cachedHash = localStorage.getItem('app-version-hash');
        
        if (cachedHash && cachedHash !== currentHash) {
          this.updateAvailable = true;
          if (this.updateCallback) {
            this.updateCallback();
          }
        }
        
        localStorage.setItem('app-version-hash', currentHash);
      })
      .catch(error => {
        console.log('Erreur lors de la vrification des mises  jour:', error);
      });
  }

  // Gnrer un hash simple du contenu
  getHash(content) {
    let hash = 0;
    for (let i = 0; i < content.length; i++) {
      const char = content.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convertir en 32-bit integer
    }
    return hash.toString();
  }

  // Appliquer la mise  jour
  applyUpdate() {
    if (this.updateAvailable) {
      // Recharger la page pour appliquer les changements
      window.location.reload();
    }
  }

  // Arrter la vrification
  stopChecking() {
    if (this.checkInterval) {
      clearInterval(this.checkInterval);
      this.checkInterval = null;
    }
  }

  // Afficher une notification de mise  jour
  showUpdateNotification() {
    if (this.updateAvailable && this.updateCallback) {
      this.updateCallback();
    }
  }
}

export default new UpdateNotifier();
